.NewPost {
	width: 80%;
	margin: 20px auto;
	padding: 12px;
	-webkit-box-shadow: 0 1px 1px -2px rgba(0,0,0,.2), 0 1px 2px 0 rgba(0,0,0,.14), 0 1px 2px 0 rgba(0,0,0,.12);
	        box-shadow: 0 1px 1px -2px rgba(0,0,0,.2), 0 1px 2px 0 rgba(0,0,0,.14), 0 1px 2px 0 rgba(0,0,0,.12);
	text-align: center;
	border-radius: 5px;
}

.NewPost:hover {
	-webkit-box-shadow: 0 3px 3px -2px rgba(0,0,0,.2), 0 3px 4px 0 rgba(0,0,0,.14), 0 1px 8px 0 rgba(0,0,0,.12);
	        box-shadow: 0 3px 3px -2px rgba(0,0,0,.2), 0 3px 4px 0 rgba(0,0,0,.14), 0 1px 8px 0 rgba(0,0,0,.12);
}

.NewPost label {
	display: block;
	margin: 10px auto;
	text-align: center;
	font-weight: bold;
}

.NewPost input,
.NewPost textarea,
.NewPost select {
	display: block;
	width: 80%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	border: 1px solid black;
	outline: none;
	font: inherit;
	margin: auto;
}

.NewPost button {
	margin: 5px 0;
	padding: 10px;
	font: inherit;
	border: 1px solid #fa923f;
	background-color: transparent;
	color: #fa923f;
	cursor: pointer;
}

.NewPost button:hover,
.NewPost button:active {
	color: white;
	background-color: #fa923f;
}


